@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&family=Noto+Sans+JP&family=Noto+Sans+KR&display=swap);
* {
  font-family: 'Noto Sans KR', 'Noto Sans JP', 'Noto Sans', sans-serif;
  font-size: calc(1em + 1vw);
  box-sizing: border-box;
}

body {
  margin: 0px;
}

#root {
  display: flex;
  justify-content: center;
}

.App {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 20vh 1fr 10vh;
  grid-template-columns: repeat(5, 1fr);
}

.Header {
  font-family: 'Noto Sans KR', sans-serif;
  grid-column: 1 / span 5;
}

.Header .title {
  font-size: 100%;
}

.Aside {
  grid-column: 1 span;
  font-size: 0.3rem;
  background-color: #f1f1f1;
  display: flex;
  padding-top: 10px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.nav-link {
  margin: 10px;
}

.Main {
  grid-column: 2 / span 4;
}

.Footer {
  grid-column: 1 / span 5;
  background-color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.2rem;
}

@media (max-width: 700px) {
  .App {
    grid-template-rows: 20vh 10vh 1fr 10vh;
  }
  .Main,
  .Aside {
    grid-column: 1 / span 5;
  }

  .Aside {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
}

